const string = `.tri{
    border: 10px solid #fff;
    border-color: #000 transparent transparent transparent;
    border-bottom: none;
    width: 0px;
    height: 0px;
    position: absolute;
    left: 50%;
    top:150px;
    margin-left: -10px;
    z-index: 10;
}
.circle{
    position: absolute;
    width: 20px;
    height:6px;
    top:-16px;
    left:-10px;
    border-radius: 10px 10px 0px 0px;
    background-color: black;
}

.eye{
    border: 2px solid #000 ;
    position: absolute;
    width: 64px;
    height: 64px;
    left:50%;
    top:100px;
    margin-left:-32px;
    background-color: #2e2e2e;
    border-radius: 50%;
}
.eye::before{
    content: "";
    display: block;
    border: 3px solid #000;
    width: 25px;
    height: 25px;
    background-color: #fff;
    border-radius: 50%;
    position: relative;
    left: 8px;
    top:4px;
}

.eye.left{
    transform: translateX(-100px);
}
.eye.right{
    transform: translateX(100px);
}
.mouth{
    width: 200px;
    height: 200px;
    position: absolute;
    left:50%;
    top:170px;
    margin-left: -100px;
}
.mouth .up{
    position: relative;
    top: -20px;
    z-index: 1;
}
.mouth .up .lip{
    background-color: #ffe600;
    border:3px solid black;
    height:30px;
    width: 100px;
    border-top-color: transparent;
    border-right-color: transparent;
    border-left-color: transparent;
    position: absolute;
    left: 50%;
    margin-left: -50px;
}

.mouth .up .lip.left{
    border-radius: 0 0 0 50px;  
    transform: rotate(-15deg) translateX(-53px);
}
.mouth .up .lip.right{
    border-radius: 0 0 50px 0;
    transform: rotate(15deg) translateX(53px);
}
.mouth .up .lip::before{
    content: '';
    display: block;
    width: 8px;
    height: 30px;
    position: absolute;
    bottom: 0px;
    background-color: #ffe600;
} 
.mouth .up .lip.left::before{
    right: -5px;
}

.mouth .up .lip.right::before{
    left: -6px;
}

.mouth .down{
    
    height: 170px;
    position: absolute;
    top:5px;
    width: 100%;
    overflow: hidden;
}

.mouth .down .yuan1{
    border: 1px solid black;
    width: 150px;
    height:1000px;
    position: absolute;
    bottom : 0;
    left : 50%;
    margin-left: -75px;
    border-radius: 75px/300px;
    background-color: #9b000a;
    overflow: hidden;
}
.mouth .down .yuan1 .yuan2{
    width: 200px;
    height:300px;
    position: absolute;
    bottom: -155px;
    left: 50%;
    margin-left: -100px;
    border-radius: 100px;
    background-color: #ff485f;
}
.face{
    position: absolute;
    left: 50%;
    top:200px;
    border: 3px solid black;
    width: 88px;
    height: 88px;
    margin-left: -44px;
}
.face > img{
    
    position: absolute;
    top: 50%;
    left: 50%;
}
.face.left >img{
    transform: rotateY(-180deg);
    transform-origin: 0 0;
}
.face.left{
    transform: translateX(-180px);
    background-color: #ff0000;
    border-radius: 50%;
}
.face.right{
    transform: translateX(180px);
    background-color: #ff0000;
    border-radius: 50%;
}
`

const demo = document.querySelector('#demo')
const demo2 = document.querySelector('#demo2')

const player = {
    id: undefined,
    n: 1,
    time: 50,
    init: () => {
        player.play()
        player.bindEvents()
    },
    events: {
        '#btnPause': 'pause',
        '#btnPlay': 'play',
        '#btnSlow': 'slow',
        '#btnNormal': 'normal',
        '#btnFast': 'fast'
    },
    bindEvents: () => {
        for (let key in player.events) {
            const value = player.events[key]
            document.querySelector(key).onclick = player[value]
        }
    },
    run: () => {
        if (player.n > string.length) {
            window.clearInterval(player.id)
            return
        }
        demo.innerHTML = string.substring(0, player.n)
        demo2.innerText = string.substring(0, player.n)
        demo2.scrollTop = 9999
        player.n += 1
    },
    play: () => {
        window.clearInterval(player.id)
        player.id = setInterval(player.run, player.time)

    },
    pause: () => {
        window.clearInterval(player.id)
    },
    slow: () => {
        player.pause()
        player.time = 100
        player.play()
    },
    normal: () => {
        player.pause()
        player.time = 50
        player.play()
    },
    fast: () => {
        player.pause()
        player.time = 0
        player.play()
    }
}
player.init()

